@columns: 12;
@atom: (100% / @columns);

@break_lg: 1200px;
@break_md: 992px;
@break_sm: 768px;
@break_xs: 576px;
@break_xxs: 380px;

@offset: 30px;
@offset_md: @offset;
@offset_sm: @offset;
@offset_xs: @offset;
@offset_xxs: @offset;

@offset_one_side: (@offset / 2);
@offset_md_one_side: (@offset_md / 2);
@offset_sm_one_side: (@offset_sm / 2);
@offset_xs_one_side: (@offset_xs / 2);
@offset_xxs_one_side: (@offset_xxs / 2);

@fields: 30px;
@fields_md: 20px;
@fields_sm: 10px;
@fields_xs: 5px;
@fields_xxs: 5px;

.wrapper-full() {
    padding-left: @fields;
    padding-right: @fields;

    .md-block( {
        padding-left: @fields_md;
        padding-right: @fields_md;
    }
    );

    .sm-block( {
        padding-left: @fields_sm;
        padding-right: @fields_sm;
    }
    );

    .xs-block( {
        padding-left: @fields_xs;
        padding-right: @fields_xs;
    }
    );

    .xxs-block( {
        padding-left: @fields_xxs;
        padding-right: @fields_xxs;
    }
    );
}

.row-offsets() {
    margin-left: (@offset_one_side * -1);
    margin-right: (@offset_one_side * -1);

    @media screen and (max-width: @break_sm) {
        margin-left: 0;
        margin-right: 0;
    }
}

.col-offsets(@type) {
    @{type}-left: @offset_one_side;
    @{type}-right: @offset_one_side;
}

.wrapper() {
    max-width: 1170px;
    margin: 0 auto;
    .wrapper-full();
}

.row-flex() {
    display: flex;
    flex-wrap: wrap;
    .row-offsets();
}

.row-float() {
    .row-offsets();
    .clearfix();
}

.row-ib() {
    .row-offsets();
}

.col() {
    box-sizing: border-box;
    word-wrap: break-word;
    .col-offsets(margin);
}

.col-float() {
    float: left;
}

.col-ib() {
    display: inline-block;
    vertical-align: top;
}

.col-padding() {
    word-wrap: break-word;
    .col-offsets(padding);
}

.size(@n) {
    @val: 100% / @columns * @n;
    width: ~ "calc(@{val} - @{offset})";
}

.size-lg(@n) {
    @val: 100% / @columns * @n;
    .lg(width, ~"calc(@{val} - @{offset_md})");
}

.size-md(@n) {
    @val: 100% / @columns * @n;
