body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

.wrapper {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

input[type="search"]::-webkit-input-placeholder {
  color: #151515;
}

input[type="search"]::-moz-placeholder {
  color: #151515;
}

html,
body {
  height: 100%;
}

body {
  font-family: 'Open Sans', sans-serif;
  display: flex;
  flex-direction: column;
}

main {
  flex-grow: 1;
}

/********************header***************************/

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}

.header .headerInner {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 73px;
}

.header__logo {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 40px;
}

.header__logo span:first-child {
  background: linear-gradient(-45deg, #fff 20%, #ffea00 20%, #ffea00 70%, #fff 70%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.header__logo span:last-child {
  background: linear-gradient(-45deg, #fff 35%, #ffea00 35%, #ffea00 70%, #fff 70%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.header__links {
  background-color: #ffffff;
  box-shadow: 2px 4px 23.75px 1.25px rgba(189, 227, 239, 0.75);
  height: 65px;
  border-radius: 50px;
  color: #151515;
  font-size: 18px;
  font-weight: 600;
  position: relative;
  display: flex;
  align-items: center;
  margin-top: 6px;
}

.header__links:hover {
  box-shadow: 2px 4px 23.75px 1.25px #bde3ef;
}

.header__links input {
  border: none;
  outline: none;
  width: 183px;
  padding-left: 72px;
  padding-right: 10px;
  border-radius: 50px;
}

.header__links button {
  border: none;
  outline: none;
  background: none;
  position: absolute;
  top: 18px;
  left: 26px;
  cursor: pointer;
}

.header__links .divider {
  background-color: #e0e0e0;
  box-shadow: -1px 5px 24px 1px rgba(189, 227, 239, 0.75);
  width: 1px;
  height: 37px;
}

.header__list {
  padding-left: 39px;
  box-sizing: border-box;
  width: 189px;
  cursor: pointer;
  position: relative;
}

.header__list::after {
  content: '';
  display: block;
  width: 10px;
  height: 10px;
  border: 2px solid #151515;
  border-top-color: transparent;
  border-right-color: transparent;
  transform: rotate(-45deg);
  position: absolute;
  top: 2px;
  right: 28px;
}

/********************banner***************************/

.banner {
  background: url('../img/bg-up.png'), url('../img/bg.png') no-repeat 0% 100% #fff;
  max-width: 1600px;
  min-height: 1052px;
  margin: 0 auto;
  background-blend-mode: multiply;
  position: relative;
}

.banner::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 398px;
  height: 125px;
  background: url('../img/logo-bg.png') no-repeat;
}

.banner__text {
  text-align: left;
  padding-top: 330px;
  font-family: 'Roboto', sans-serif;
  font-size: 115px;
  font-weight: 700;
  line-height: 120px;
  width: 44%;
  margin-left: auto;
  margin-right: 0;
}

.banner__text-dark {
  color: #090000;
}

.banner__text-light {
  color: #fff;
}

.banner__text-discount {
  font-size: 50px;
  font-weight: 500;
  color: #fff;
  text-transform: uppercase;
  margin-left: 8px;
  line-height: 91px;
}

.banner__text-discount span {
  color: #fbf934;
  margin-right: 13px;
}

.banner__btn {
  font-size: 20px;
  font-weight: 600;
  text-align: center;
  border: none;
  outline: none;
  width: 202px;
  height: 60px;
  border-radius: 30px;
  cursor: pointer;
  margin-top: 70px;
  margin-left: 6px;
  background-color: #fcfcfc;
  box-shadow: 2px 4px 23.75px 1.25px rgba(189, 227, 239, 0.75);
  position: relative;
  transition: background 0.6s ease, color 0.2s ease;
  line-height: 60px;
}

.banner__btn a {
  text-decoration: none;
  color: #151515;
  margin-left: -36px;
}

.banner__btn::after {
  content: '\f30b';
  font-family: "Font Awesome 5 Free";
  color: #7048e2;
  font-size: 18px;
  position: absolute;
  top: 0;
  right: 53px;
}

.banner__btn:hover {
  box-shadow: 2px 4px 23.75px 1.25px #bde3ef;
}

.banner__btn:hover a {
  color: #7048e2;
}

.banner__btn:active {
  position: relative;
  top: 2px;
  left: 2px;
  box-shadow: none;
}

.banner__decoration-square {
  width: 24px;
  height: 24px;
  border: 5px solid #fff;
  border-radius: 5px;
  position: absolute;
  bottom: 26%;
  right: 6.5%;
  transform: rotate(60deg);
}

.banner__decoration-circle {
  width: 14px;
  height: 14px;
  opacity: 0.5;
  border: 6px solid #ffffff;
  border-radius: 50%;
  position: absolute;
  top: 17.5%;
  left: 53%;
}

/********************trends***************************/

.trends {
  max-width: 1380px;
  box-shadow: 2px 4px 47.5px 2.5px rgba(237, 235, 253, 0.75);
  background-color: #fff;
  position: relative;
  margin: 0 auto;
  margin-top: -37px;
  padding-top: 82px;
  padding-bottom: 90px;
}

.trends__decoration-circle {
  border: 6px solid #b4a1f7;
  border-radius: 50%;
  width: 56px;
  height: 56px;
  position: absolute;
  top: 32%;
  left: -2%;
  z-index: -1;
}

.trends__decoration-zip {
  position: absolute;
  top: 4.5%;
  right: 9.5%;
}

.trends__decoration-triangle {
  width: 0;
  height: 0;
  border-top: 40px solid #f3efff;
  border-left: 22px solid transparent;
  border-right: 22px solid transparent;
  transform: rotate(-15deg);
  position: absolute;
  top: 9.5%;
  left: 19%;
}

.trends__header {
  color: #333;
  font-size: 50px;
  font-weight: 700;
  text-align: center;
  position: relative;
  z-index: 5;
}

.trends__header::after {
  content: '';
  display: block;
  width: 6px;
  height: 105px;
  background-color: #dcfedb;
  transform: rotate(-25deg);
  position: absolute;
  top: 0;
  right: 35%;
  z-index: -1;
}

.trends__items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  margin: 0;
  margin-top: 90px;
}

.trends__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.333333333333336% - 30px);
  color: #777;
  text-align: center;
  text-decoration: none;
  position: relative;
  margin-bottom: 49px;
  padding-bottom: 73px;
}

.trends__item .metaBlock {
  display: none;
}

.trends__item .metaBlock .metaLikes {
  position: absolute;
  top: 23px;
  left: 20px;
  z-index: 10;
}

.trends__item .metaBlock .metaLikes:hover {
  transform: scale(1.1);
  transition-duration: .6s;
}

.trends__item .metaBlock .metaReviews {
  position: absolute;
  top: 23px;
  right: 20px;
  z-index: 10;
}

.trends__item .metaBlock .metaReviews:hover {
  transform: scale(1.1);
  transition-duration: .6s;
}

.trends__descr {
  font-size: 18px;
  font-weight: 400;
  margin-top: 12px;
}

.trends__price {
  color: #333;
  font-size: 25px;
  font-weight: 600;
  margin-top: 7px;
}

.trends__img {
  height: 298px;
  overflow: hidden;
  position: relative;
}

.trends__img img {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
}

.trends__item:hover {
  background-color: #ffffff;
  box-shadow: 2px 4px 47.5px 2.5px rgba(221, 223, 224, 0.75);
  color: #333;
}

.trends__item:hover .metaBlock {
  display: block;
}

.trends__item:hover .trends__price {
  color: #8c84f9;
}

.trends__btn {
  display: none;
  font-size: 20px;
  font-weight: 600;
  text-align: center;
  border: none;
  outline: none;
  width: 202px;
  height: 60px;
  border-radius: 30px;
  cursor: pointer;
  background: linear-gradient(26deg, #8c84f9 27%, #a165f4 73%);
  box-shadow: -1px 5px 24px 1px rgba(240, 239, 254, 0.75);
  color: #fff;
  padding-top: 16px;
  box-sizing: border-box;
  position: absolute;
  bottom: -26px;
  left: 0;
  right: 0;
  margin: 0 auto;
  transition: .6s;
}

.trends__btn:hover {
  background: linear-gradient(26deg, #8c84f9 50%, #a165f4 80%);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.4);
}

.trends__item:hover .trends__btn {
  display: block;
}

/********************brands***************************/

.brands {
  position: relative;
}

.brands__decoration-zip {
  position: absolute;
  top: 56%;
  right: 17%;
}

.brands .brandsInner {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.brands__descr {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(50% - 30px);
  padding-right: 60px;
}

.brands__header {
  color: #333;
  font-size: 50px;
  font-weight: 700;
  text-align: center;
  position: relative;
  text-align: left;
  margin-top: 245px;
}

.brands__header::after {
  content: '';
  display: block;
  width: 6px;
  height: 105px;
  background-color: #dbffff;
  transform: rotate(-25deg);
  position: absolute;
  top: 0;
  right: 45%;
  z-index: -1;
}

.brands__info {
  color: #333;
  font-size: 25px;
  font-weight: 400;
  line-height: 34px;
  margin-top: 12px;
  padding-left: 5px;
}

.brands__info a {
  text-decoration: none;
  color: #8d83f9;
}

.brands__text {
  color: #888;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  margin-top: 24px;
  padding-left: 3px;
}

.brands__img {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(50% - 30px);
  min-height: 860px;
  position: relative;
  overflow: hidden;
}

.brands__img .bgImg {
  background: url('../img/brand-bg.png') no-repeat 50% 45%;
  width: 399px;
  height: 399px;
  position: absolute;
  top: 45px;
  right: 0;
}

.brands__img .bgCircle {
  width: 303px;
  height: 303px;
  background-color: #fff;
  border-radius: 50%;
  margin: 0 auto;
  margin-top: 50px;
}

.brands__img .bgIphone {
  position: absolute;
  top: 116px;
  right: 135px;
}

/********************showcase***************************/

.showcase {
  max-width: 1380px;
  box-shadow: 2px 4px 47.5px 2.5px rgba(237, 235, 253, 0.75);
  background-color: #fff;
  position: relative;
  margin: 0 auto;
  padding-top: 82px;
  z-index: 10;
}

.showcase .tabs-panel {
  display: none;
}

.showcase .tabs-panel.active {
  display: flex;
}

.showcase__header {
  color: #333;
  font-size: 50px;
  font-weight: 700;
  text-align: center;
  position: relative;
}

.showcase__header::after {
  content: '';
  display: block;
  width: 6px;
  height: 105px;
  background-color: #fcffae;
  transform: rotate(-25deg);
  position: absolute;
  top: 0;
  right: 35%;
  z-index: -1;
}

.showcase__list {
  box-shadow: -1px 5px 48px 3px rgba(239, 239, 240, 0.75);
  background-color: #fff;
  border-radius: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  color: #333333;
  font-size: 15px;
  font-weight: 600;
  padding: 22px 120px;
  margin: 0 auto;
  margin-top: 90px;
  box-sizing: border-box;
}

.showcase__list li {
  padding: 0 10px;
}

.showcase__list li:hover {
  color: #b394fa;
  text-shadow: -2px 9px 5px rgba(213, 200, 247, 0.75);
  cursor: pointer;
  transition-duration: .4s;
}

.showcase__list .active {
  color: #b394fa;
  text-shadow: -2px 9px 5px rgba(213, 200, 247, 0.75);
}

.showcase__items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  margin: 0;
  margin-top: 85px;
  padding-bottom: 33px;
}

.showcase__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.333333333333336% - 30px);
  color: #777;
  text-align: center;
  text-decoration: none;
  position: relative;
  margin-bottom: 93px;
}

.showcase__price {
  color: #333;
  font-size: 25px;
  font-weight: 600;
  margin-top: 10px;
}

.showcase__descr {
  font-size: 18px;
  font-weight: 400;
  margin-top: 20px;
}

.showcase__img {
  height: 267px;
  overflow: hidden;
  position: relative;
}

.showcase__img img {
  top: 0;
  bottom: 0;
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
}

/********************feedback***************************/

.feedback {
  max-width: 1600px;
  margin: 0 auto;
  background: linear-gradient(45deg, #8c84f9 0%, #a165f4 100%);
  margin-top: -74px;
  padding-top: 160px;
  padding-bottom: 125px;
}

.feedback__header {
  color: #fff;
  font-size: 50px;
  font-weight: 700;
  text-align: center;
  z-index: 10;
}

.feedback__headerBack {
  font-family: 'Roboto', sans-serif;
  color: rgba(254, 254, 254, 0.04);
  font-size: 150px;
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
  margin-top: -122px;
  letter-spacing: 3px;
}

.feedback__form {
  margin: 0 auto;
  width: 53%;
  margin-top: 10px;
  position: relative;
}

.feedback__form input[type="email"] {
  padding: 0 210px 0 27px;
  background-color: #fff;
  width: 100%;
  height: 71px;
  box-sizing: border-box;
  border: none;
  outline: none;
  border-radius: 35px;
  font-size: 20px;
  font-weight: 400;
}

.feedback__form input[type="submit"] {
  text-align: center;
  border: none;
  outline: none;
  width: 202px;
  height: 60px;
  border-radius: 30px;
  cursor: pointer;
  background: linear-gradient(28deg, #8c84f9 31%, #9c6cf5 69%);
  color: #fffefe;
  font-size: 20px;
  font-weight: 600;
  position: absolute;
  top: 5px;
  right: 3px;
}

.feedback__form input[type="submit"]:hover {
  background: linear-gradient(26deg, #8c84f9 50%, #a165f4 80%);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.4);
}

/********************footer***************************/

.footer__header {
  color: #222;
  font-family: 'Roboto', sans-serif;
  font-size: 40px;
  font-weight: 500;
  text-align: center;
  margin-top: 65px;
}

.footer__links {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 35px;
}

.footer__links a {
  color: #403f3f;
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
  padding: 5px 25px;
}

.footer__links a:hover {
  color: #b394fa;
}

.footer__social {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 46px;
  margin-bottom: 50px;
}

.footer__social .socialWrap {
  width: 53px;
  height: 53px;
  border: 2px solid #ecebed;
  background-color: #fff;
  border-radius: 50%;
  margin: 0 15px;
  text-align: center;
  padding-top: 12px;
  box-sizing: border-box;
}

.footer__social .socialWrap i {
  color: #555;
  font-size: 23px;
  line-height: 26px;
}

.footer__social .socialWrap:hover {
  background: linear-gradient(27deg, #8c84f9 0%, #9b6df5 99%);
  box-shadow: 4px 7px 19px 1px rgba(207, 207, 208, 0.75);
  border: 1px solid #ecebed;
}

.footer__social .socialWrap:hover i {
  color: #fff;
}

.footer .copyright {
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  color: #777777;
  font-size: 15px;
  background-color: #f6f5f7;
  padding: 25px 0;
}

@media screen and (max-width: 1200px) {
  .banner__text {
    font-size: 95px;
  }

  .banner__text {
    line-height: 90px;
  }

  .brands__descr {
    width: calc(41.66666666666667% - 30px);
  }

  .brands__descr {
    padding-right: 0;
  }

  .brands__img {
    width: calc(58.333333333333336% - 30px);
  }

  .feedback__headerBack {
    font-size: 80px;
  }

  .feedback__headerBack {
    margin-top: -80px;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .banner__text {
    font-size: 70px;
  }

  .banner__text {
    line-height: 70px;
  }

  .trends__item {
    width: calc(50% - 30px);
  }

  .brands__descr {
    width: calc(100% - 30px);
  }

  .brands__header {
    margin-top: 50px;
  }

  .brands__img {
    width: calc(100% - 30px);
  }

  .brands__img .bgImg {
    right: 50px;
  }

  .brands__img .bgIphone {
    right: 228px;
  }

  .showcase__item {
    width: calc(50% - 30px);
  }

  .feedback__form {
    width: 90%;
  }

  .feedback__form input[type="email"] {
    border-radius: 5px;
  }

  .feedback__form input[type="email"] {
    padding-right: 100px;
  }

  .feedback__form input[type="email"] {
    padding-left: 10px;
  }

  .feedback__form input[type="email"] {
    font-size: 14px;
  }

  .feedback__form input[type="submit"] {
    width: 90px;
  }

  .feedback__form input[type="submit"] {
    border-radius: 5px;
  }

  .feedback__form input[type="submit"] {
    font-size: 14px;
  }
}

@media screen and (max-width: 768px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .banner__text {
    font-size: 55px;
  }

  .banner__text {
    line-height: 50px;
  }

  .banner__text {
    padding-top: 310px;
  }

  .banner__btn {
    margin-top: 30px;
  }

  .trends {
    padding-top: 40px;
  }

  .trends__items {
    margin-left: 0;
    margin-right: 0;
  }

  .brands .brandsInner {
    margin-left: 0;
    margin-right: 0;
  }

  .brands__img .bgImg {
    right: 20px;
  }

  .brands__img .bgIphone {
    right: 198px;
  }

  .showcase {
    padding-top: 40px;
  }

  .showcase__list {
    padding: 22px;
  }

  .showcase__list {
    margin-top: 40px;
  }

  .showcase__items {
    margin-left: 0;
    margin-right: 0;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .header .headerInner {
    flex-direction: column;
  }

  .header .headerInner {
    align-items: center;
  }

  .header .headerInner {
    padding-top: 110px;
  }

  .header__links {
    margin-top: 35px;
  }

  .header__links input {
    width: 175px;
  }

  .header__list {
    width: 175px;
  }

  .header__list {
    padding-left: 20px;
  }

  .banner {
    background: linear-gradient(27deg, #1100ff 0%, #aa55f3 100%);
  }

  .banner {
    min-height: 900px;
  }

  .banner::after {
    width: 100%;
  }

  .banner__text {
    width: 100%;
  }

  .banner__text {
    text-align: center;
  }

  .banner__btn {
    margin: 0 auto;
  }

  .banner__btn {
    margin-top: 70px;
  }

  .trends__header {
    font-size: 40px;
  }

  .trends__items {
    justify-content: center;
  }

  .trends__item {
    width: calc(83.33333333333334% - 30px);
  }

  .trends__item {
    background-color: #ffffff;
  }

  .trends__item {
    box-shadow: 2px 4px 47.5px 2.5px rgba(221, 223, 224, 0.75);
  }

  .trends__item .metaBlock {
    display: block;
  }

  .trends__btn {
    display: block;
  }

  .brands__header {
    font-size: 40px;
  }

  .brands__img .bgImg {
    right: 0;
  }

  .brands__img .bgImg {
    left: 0;
  }

  .brands__img .bgIphone {
    right: 0;
  }

  .showcase__header {
    font-size: 40px;
  }

  .showcase__list {
    align-items: center;
  }

  .showcase__list {
    flex-direction: column;
  }

  .showcase__list li {
    padding: 5px 10px;
  }

  .showcase__items {
    justify-content: space-around;
  }

  .showcase__item {
    width: calc(83.33333333333334% - 30px);
  }

  .feedback__header {
    font-size: 30px;
  }

  .feedback__headerBack {
    font-size: 50px;
  }

  .feedback__headerBack {
    margin-top: -50px;
  }

  .footer__social .socialWrap {
    margin: 0 5px;
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .header__links {
    font-size: 14px;
  }

  .header__links input {
    width: 150px;
  }

  .header__list {
    width: 150px;
  }

  .trends__item {
    width: calc(100% - 30px);
  }

  .feedback__headerBack {
    font-size: 40px;
  }

  .feedback__headerBack {
    margin-top: -40px;
  }
}